import React from 'react'
import styled from 'styled-components'
import Text from '../components/text'
import Container from '../components/container'

const ShotWrapper = styled.div`
	background: #F5F5F5;
	margin-bottom: 64px;
	margin-left: 64px;
	margin-right: 64px;
	padding-left: 64px;
	padding-right: 64px;
	padding-top: 64px;
	padding-bottom: 64px;

	@media (max-width: 1200px) {
		margin-bottom: 32px;
		margin-left: 0;
		margin-right: 0;
		padding-left: 32px;
		padding-right: 32px;
		padding-top: 32px;
		padding-bottom: 32px;
	}
`
const Image = styled.img`
	box-shadow: 0 2px 6px 0 rgba(0,0,0,.10), 0 11px 54px 0 rgba(0,0,0,.05);
	border-radius: 4px;
	display: block;
	margin: 0 auto;
`
const Caption = styled(Text)`
	max-width: 640px;
`
const CaptionWrapper = styled.div`
	margin: 32px auto 0 auto;
	max-width: 1280px;
`

const Shot = (props) => {
	return(
		<ShotWrapper>
			<Image src={props.src} />

			{props.caption
				? <CaptionWrapper>
					<Caption>
						{props.caption}
					</Caption>
				</CaptionWrapper>
				: null
			}
		</ShotWrapper>
	)
}

Shot.defaultProps = {
	spaced: true,
	boxed: true
}

export default Shot



